export default "import React from 'react';\r\nimport {\r\n  Area,\r\n  Bar,\r\n  CartesianGrid,\r\n  ComposedChart,\r\n  Legend,\r\n  Line,\r\n  ResponsiveContainer,\r\n  Tooltip,\r\n  XAxis,\r\n  YAxis,\r\n} from 'recharts';\r\nimport data from './data';\r\n\r\nconst ComposedChartWithAxisLabels = () => (\r\n  <ResponsiveContainer width='100%' height={200}>\r\n    <ComposedChart\r\n      data={data}\r\n      margin={{top: 10, right: 0, left: -25, bottom: 0}}>\r\n      <XAxis dataKey='name' />\r\n      <YAxis />\r\n      <Tooltip />\r\n      <Legend />\r\n      <CartesianGrid stroke='#f5f5f5' />\r\n      <Area type='monotone' dataKey='amt' fill='#F04F47' stroke='#F04F47' />\r\n      <Bar dataKey='pv' barSize={20} fill='#4299E1' />\r\n      <Line type='monotone' dataKey='uv' stroke='#59AA2B' />\r\n    </ComposedChart>\r\n  </ResponsiveContainer>\r\n);\r\n\r\nexport default ComposedChartWithAxisLabels;\r\n";